		goodsDataList(type_id) {
			this.$util.get('/login/sgetNav?types=goods').then(res => {
				this.cateList = res.data
			})
			this.$util.get('/shop/getGoods?typeId=' + type_id).then(res => {
				this.goodsList = res.data.data
			})
			console.log('?');
		},

		//筛选点击
		tabClick(index) {
			if (this.filterIndex === index && index !== 2) {
				return;
			}
			this.filterIndex = index;
			
			if (index === 2) {
				const order =  this.priceOrder === 1 ? 2 : 1
				switch(order){
					case 1:
					//价格升序
					this.goodsList.sort(function(x,y){
						return x.price - y.price
					})
					break
					case 2:
					//价格降序
					this.goodsList.sort(function(x,y){
						return y.price - x.price
					})
				}

				
				this.priceOrder = order;
			} else {
				this.priceOrder = 0;
			}
			//按销量排序
			if(index==1){
				this.goodsList.sort(function(x,y){
					return x.num - y.num
				})
			}
			
			uni.pageScrollTo({
				duration: 300,
				scrollTop: 0
			})
			this.loadData('refresh', 1);
			uni.showLoading({
				title: '正在加载'
			})
		},
		//显示分类面板
		toggleCateMask(type) {
			let timer = type === 'show' ? 10 : 300;
			let state = type === 'show' ? 1 : 0;
			this.cateMaskState = 2;
			setTimeout(() => {
				this.cateMaskState = state;
			}, timer)
		},
		//分类点击
		changeCate(item) {
			console.log(item, '分类点击')
			this.cateId = item.id;
			this.toggleCateMask();
			uni.pageScrollTo({
